import { Landing } from './Landing';
import { Survey } from './Survey';
import { Submit } from './Submit';
import { SurveyContextProvider, useSurvey } from './useSurvey';

export function App() {

  function Main() {

    const { appState: survey } = useSurvey()

    return <>
      {survey.page === 'Landing' ? <Landing /> : <></>}
      {survey.page === 'Survey' ? <Survey /> : <></>}
      {survey.page === 'Submit' ? <Submit /> : <></>}
    </>
  }

  return <>

    <SurveyContextProvider>
      <Main />
    </SurveyContextProvider>
  </>
}

export default App;
