import { Button, ProgressBar } from 'react-bootstrap';
import { SurveyQuestion } from './SurveyQuestion';
import { useSurvey } from './useSurvey';


export function Survey() {

    const { appState: survey, next, prev } = useSurvey()

    const questions = survey.variants[survey.currVariantName]

    return <>
        <ProgressBar now={survey.progress} max={questions.length} />
        <SurveyQuestion />
        <center>
            {survey.progress === 0 ? <></> : <Button onClick={prev} size="lg" style={{ margin: '10px' }}>Back</Button>}
            <Button onClick={next} size="lg" style={{ margin: '10px' }}>Next</Button>
        </center>
    </>
}