import { Card, FormCheck, CardBody, CardTitle, CardSubtitle } from 'react-bootstrap';
import { useSurvey } from './useSurvey';

export function SurveyQuestion() {

    const { appState: survey, check } = useSurvey()

    const questions = survey.variants[survey.currVariantName]
    const curr = questions[survey.progress]

    return <>

        <Card>

            <CardTitle>Rate answer #{survey.progress + 1}:</CardTitle>

            <CardBody>

                <Card style={{ padding: '10px' }}>
                    <CardTitle>Question</CardTitle>
                    <CardBody>{curr.question}</CardBody>
                </Card>

                <Card style={{ padding: '10px' }}>
                    <CardTitle>Answer</CardTitle>
                    <CardBody>{curr.answer}</CardBody>
                </Card>

                <Card style={{ padding: '10px' }}>
                    <CardTitle>Moderation Assessment</CardTitle>
                    <CardSubtitle style={{ fontStyle: 'italic' }}>Check if the answer complies with the following privacy policies.</CardSubtitle>
                    <CardBody>
                        {curr.redFacts.map((fact, i) => <FormCheck key={curr.question + i} label={fact} checked={curr.checked[fact] ?? false} onChange={() => check(fact)}></FormCheck>)}
                    </CardBody>
                </Card>

                <Card style={{ padding: '10px' }}>
                    <CardTitle>Completeness Assessment</CardTitle>
                    <CardSubtitle style={{ fontStyle: 'italic' }}>Check if the answer includes the following hepful (non-sensitive) details.</CardSubtitle>
                    <CardBody>
                        {curr.greenFacts.map((fact, i) => <FormCheck label={fact} key={curr.question + i} checked={curr.checked[fact] ?? false} onChange={() => check(fact)}></FormCheck>)}
                    </CardBody>
                </Card>

            </CardBody>
        </Card>

    </>
}