import { ReactNode, useEffect, useState, } from "react"
import React, { createContext, useContext } from 'react';

export type Page = 'Landing' | 'Survey' | 'Submit'

export type AppState = {
    currVariantName: string
    variants: { [variantName: string]: SurveyQuestion[] }
    progress: number
    page: Page
}

export type SurveyQuestion = {
    question: string
    answer: string
    redFacts: string[]
    greenFacts: string[]
    checked: { [fact: string]: boolean }
}

interface AppContext {
    appState: AppState,
    setAppState: (survey: AppState) => void
    setPage: (page: Page) => void
    next: () => void
    prev: () => void
    check: (fact: string) => void
    setVariant: (variantName: string) => void
};

const surveyContext = createContext<AppContext | undefined>(undefined)

export const SurveyContextProvider = ({ children }: { children: ReactNode }) => {

    const [appState, setAppState] = useState<AppState>({
        currVariantName: 'placeholder',
        variants: { placeholder: [{ question: 'Q?', answer: 'A!', redFacts: ['red'], greenFacts: ['green'], checked: {} }] },
        progress: 0,
        page: 'Landing',
    })

    useEffect(() => {
        fetch('./variants.json').then(x => x.json()).then(x => {
            setAppState(state => ({ ...state, variants: x }))
        })
    }, [])

    const next = () => setAppState(survey => {

        const questions = survey.variants[survey.currVariantName]

        const progress = Math.min(questions.length, survey.progress + 1)

        return {
            ...survey,
            progress,
            page: progress === questions.length ? 'Submit' : survey.page,
        }
    })

    const prev = () => setAppState(survey => {

        const questions = survey.variants[survey.currVariantName]
        const progress = Math.max(0, survey.progress - 1)

        return {
            ...survey,
            progress,
            page: progress < questions.length ? 'Survey' : survey.page
        }
    })

    const setPage = (page: Page) => setAppState(survey => ({
        ...survey,
        page,
    }))

    const check = (fact: string) => setAppState(survey => {

        console.log('Checking', fact)

        const variant = survey.variants[survey.currVariantName]

        const curr = variant[survey.progress]
        const { checked } = curr
        const newChecked = { ...checked, [fact]: !checked[fact] }

        const variantNew = [
            ...variant.slice(0, survey.progress),
            { ...curr, checked: newChecked },
            ...variant.slice(survey.progress + 1)
        ]

        const result: AppState = {
            ...survey,
            variants: { ...survey.variants, [survey.currVariantName]: variantNew }
        }

        return result
    })

    const setVariant = (variantName: string) => setAppState(state => {

        // clear cheked flags
        const variant = state.variants[variantName].map(x => ({ ...x, checked: {} }))

        const newAppState: AppState = {
            ...state,
            currVariantName: variantName,
            variants: { ...state.variants, [variantName]: variant },
            progress: 0,
        }

        return newAppState
    })

    return <surveyContext.Provider value={{ setVariant, appState, setAppState, setPage, next, prev, check }}>
        {children}
    </surveyContext.Provider>

}

export const useSurvey = () => {
    const context = useContext(surveyContext);
    if (!context) throw new Error('useSurvey must be used within SurveyProvider');
    return context;
}


