import { Button, Card, Alert, CardBody, Dropdown } from 'react-bootstrap';
import { useSurvey } from './useSurvey';


export function Landing() {

    const { setPage, setVariant, appState } = useSurvey()


    function startSurvey(variantName: string) {
        setVariant(variantName)
        setPage('Survey')
    }

    return <>

        <Card>

            <Card.Title className="p-4 shadow-sm">Content Moderation Survey</Card.Title>

            <CardBody>

                <p>
                    A content moderator answers user questions following a privacy policy.

                    The content moderator tries to include as much useful information in the answers as permitted by the privacy policy.

                    You will help us evaluate the efficiency of this content moderator, by rating the answers that he gave to some user questions.
                </p>

                <br />
                <p>You will rate each answer based on:</p>

                <ol>
                    <li>Adherence to a privacy policy,</li>
                    <li>inclusion of as much useful non-sensitive info as possible.</li>
                </ol>

            </CardBody>

            <Alert variant='warning'>
                Please do <strong>NOT</strong> refresh the page while taking the survey.
            </Alert>

            <Alert variant='warning'>
                This is a static website, after completing the survey, you should kindly download the resulting JSON file and <strong>mail it to us</strong>.
            </Alert>

            <center>
                <Dropdown style={{ width: '500px' }}>
                    <Dropdown.Toggle
                        as={Button}
                        variant="primary"
                        size='lg'
                        style={{ width: '500px' }}>
                        Take the survey.
                    </Dropdown.Toggle>

                    <Dropdown.Menu style={{ width: '500px' }}>
                        {Object.keys(appState.variants).map((name, i) => <Dropdown.Item
                            eventKey={i}
                            key={'variant' + i}
                            onClick={() => startSurvey(name)}>
                            {name}
                        </Dropdown.Item>)}
                    </Dropdown.Menu>
                </Dropdown>
            </center>

        </Card>
    </>
}